  src: url("../fonts/LatoSemiBold/LatoSemiBold.eot?#iefix") format("embedded-opentype"), url("../fonts/LatoSemiBold/LatoSemiBold.woff") format("woff"), url("../fonts/LatoSemiBold/LatoSemiBold.woff2") format("woff2"), url("../fonts/LatoSemiBold/LatoSemiBold.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
  font-display: swap;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.2;
  font-family: "LatoRegular", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.header_fon {
  background: transparent url("../img/header_fon.jpg") center center no-repeat;
  background-size: cover;
  min-height: 740px;
  position: relative;
}

.header_fon:before {
  content: '';
  background-color: rgba(15, 15, 15, 0.7);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
}

.top_line {
  padding: 24px 0;
}

.container_wrap {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 1400px;
  margin: 0 auto;
}

.logo {
  padding: 0;
  margin: 0;
}

.logo img {
  display: inline-block;
  width: 100%;
  height: auto;
}

.top_line_menu {
  padding: 0;
  margin: 0;
}

.top_line_menu ul {
  list-style: none;
  display: flex;
}

.top_line_menu ul li a {
  font-family: "LatoBlack", sans-serif;
  font-size: 1.0625rem;
  line-height: 1.2;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
  padding: 10px 20px;
  border-radius: 18px;
  transition: all 0.3s ease;
}

.top_line_menu ul li a:hover {
  background-color: #ffc61a;
}

.header_content {
  margin-top: 40px;
  text-align: center;
  position: relative;
}

.header_content_car {
  background: url(../img/header_fon_car.jpg.png) center center no-repeat;
  content: '';
  z-index: 3;
  position: absolute;
  bottom: -15%;
  left: 50%;
  width: 826px;
  height: 484px;
  margin-left: -413px;
}

.header_title {
  color: #ffc61a;
  font-size: 2.1875rem;
  text-transform: uppercase;
}

.header_phone,
.header_link {
  color: #fff;
  font-size: 4.5rem;
  text-decoration: none;
  display: block;
  text-transform: uppercase;
  transition: all 0.3s ease;
}

.header_link {
  font-size: 1.25rem;
  line-height: 1.2;
  letter-spacing: 16.0px;
}

.header_phone:hover,
.header_link:hover {
  color: gray;
}

.yellow_block_wrap {
  display: flex;
  justify-content: space-between;
}

.yellow_left {
  flex-basis: 63%;
  padding-right: 215px;
}

.yellow_right {
  flex-basis: 37%;
}

.yellow_descr {
  font-size: 2.1875rem;
  font-family: "LatoBlack", sans-serif;
  text-transform: uppercase;
  margin-bottom: 25px;
}

.yellow_title {
  font-size: 3.125rem;
  font-family: "LatoBlack", sans-serif;
  text-transform: uppercase;
  margin-bottom: 45px;
}

.yellow_text {
  font-size: 1.125rem;
  margin-bottom: 60px;
}

.bookacab_block {
  width: 100%;
}

.bookacab_block_title {
  padding: 5px 10px;
  background-color: #000000;
  font-size: 2.125rem;
  text-transform: uppercase;
  color: #fff;
  font-family: "LatoBlack", sans-serif;
}

.form_book {
  padding: 30px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #fff;
}

.form_book_input {
  padding: 5px 10px;
  background-color: #fff;
  font-size: 1.25rem;
  color: #000;
  font-family: "LatoSemiBold", sans-serif;
  border: none;
  border-bottom: 1px solid #e4e4e4;
  width: 48%;
  height: 40px;
  line-height: 40px;
  margin-bottom: 20px;
}

.form_book_input_choice {
  width: 100%;
  margin-bottom: 44px;
}

.form_book_submit {
  font-family: "BebasNeueBold", sans-serif;
  font-size: 1.8125rem;
  padding: 11px 0;
  display: block;
  width: 100%;
  background-color: #131313;
  color: #fff;
  text-transform: uppercase;
  border-radius: 10px;
  cursor: pointer;
}

.sector_button {
  display: inline-block;
  padding: 14px 34px;
  font-family: "BebasNeueBold", sans-serif;
  font-size: 1.8125rem;
  color: #fff;
  border-radius: 10px;
  background-color: #000;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: 10px;
  cursor: pointer;
}

.block_tarif {
  padding: 60px 0 80px 0;
}

.tarif_wrap {
  display: flex;
  justify-content: space-between;
}

.tarif_items {
  margin-top: 75px;
  flex-basis: 32%;
  background-color: #f5f4f2;
  padding: 120px 40px 25px 40px;
  text-align: center;
  position: relative;
  transition: all 0.3s ease;
}

.tarif_items:before {
  right: -35px;
  bottom: -7px;
  position: absolute;
  width: 0;
  height: 0;
  border-left: 55px solid transparent;
  border-right: 55px solid transparent;
  border-top: 55px solid #000;
  transform: rotate(-45deg);
  color: #ffc61a;
  content: '';
  opacity: 0;
}

.tarif_items:after {
  right: 12px;
  bottom: 9px;
  position: absolute;
  font-size: 1.875rem;
  content: '\f005';
  font-family: 'fontawesome';
  color: #ffc61a;
  opacity: 0;
}

.tarif_items:hover {
  background-color: #ffc61a;
}

.tarif_items:hover:before,
.tarif_items:hover:after {
  opacity: 1;
}

.tarif_items:hover .tarif_price {
  color: #000;
}

.tarif_items:hover .sector_button {
  color: #fff;
  background-color: #1b1b1b;
}

.tarif_title {
  font-size: 1.875rem;
  text-transform: uppercase;
  font-family: "LatoBlack", sans-serif;
  margin-bottom: 30px;
}

.tarif_terms {
  margin-bottom: 40px;
  font-size: 1.125rem;
}

.tarif_price {
  font-size: 2.5rem;
  text-transform: uppercase;
  font-family: "LatoBlack", sans-serif;
  margin-bottom: 40px;
  color: #ffc61a;
}

.tarif_items .sector_button {
  background-color: #ffc61a;
  color: #000;
  font-weight: 800;
}

.tarif_items_car {
  position: absolute;
  left: 0;
  transform: translateY(-115%);
}

.block_tarif {
  text-align: center;
}

.tarif_title {
  font-size: 2.75rem;
  margin-bottom: 50px;
  display: inline-block;
  font-family: "LatoBlack", sans-serif;
}

.tarif_title span {
  display: block;
  color: #ffc61a;
  font-size: 2.1875rem;
}

.block {
  padding: 160px 0 50px 0;
}

.block_order {
  background-color: #ffc61a;
}

.heagerclass {
  font-size: 2rem;
}

.heagerclass {
  background-color: red;
  padding: 50px 0;
}

.heagerclass_title {
  color: #fff;
}

.footer_wrap {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
}

.footer_items {
  width: 30%;
  background-color: yellow;
}

.heagerclass {
  background-color: red;
  padding: 50px 0;
}

.heagerclass_title {
  color: #fff;
}

